import Vue from 'vue'
import App from './App.vue'
import router from './router'
import {CountDown, TreeSelect , Divider , Loading , Dialog ,Tag , Uploader,Overlay ,AddressList,Area ,AddressEdit ,Switch,SwitchCell ,ShareSheet , Grid, GridItem ,SubmitBar ,Checkbox, CheckboxGroup, SwipeCell , Stepper , Button,NavBar,Icon, Image as VanImage , Form ,Field , Popup ,Cell, CellGroup ,Toast  ,Tabbar, TabbarItem ,Swipe, SwipeItem , Lazyload ,Tab, Tabs , Card , GoodsAction, GoodsActionIcon, GoodsActionButton } from 'vant';
import 'lib-flexible/flexible'
import axios from 'axios'
import VueAxios from 'vue-axios'


Vue
   .use(Button)
   .use(NavBar)
   .use(Icon)
   .use(VanImage)
   .use(Form)
   .use(Field)
   .use(Popup)
   .use(Cell, CellGroup)
   .use(Toast)
   .use(Tabbar)
   .use(TabbarItem)
   .use(Swipe)
   .use(SwipeItem)
   .use(Lazyload)
   .use(Tab)
   .use(Tabs)
   .use(Card)
   .use(GoodsAction)
   .use(GoodsActionButton)
   .use(GoodsActionIcon)
   .use(Stepper)
   .use(SwipeCell)
   .use(Checkbox)
   .use(CheckboxGroup)
   .use(SubmitBar)
   .use( Grid)
   .use(GridItem)
   .use(ShareSheet)
   .use(SwitchCell)
   .use(Switch)
   .use(AddressEdit)
   .use(Area)
   .use(AddressList)
   .use(Overlay)
   .use(Uploader)
   .use(Tag)
   .use(Dialog)
   .use(Loading)
   .use(Divider)
   .use(TreeSelect)
   .use(CountDown)
 



Vue.config.productionTip = false

// 设置Vue全局密钥
Vue.prototype.appkey  = 'U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=';

// 全局设置用户key
Vue.prototype.useId    = window.localStorage.getItem('useId');
Vue.prototype.passWord = window.sessionStorage.getItem('passWord');




// 配置请求基础路径
axios.defaults.baseURL = 'http://www.kangliuyong.com:10002';

// axios拦截器，在发起请求之前执行
axios.interceptors.request.use(config => {

//   //  处理post请求参数，进行参数序列化
  if(config.method == 'post'){
    //  
    // 序列化post请求参数，进行参数系列化
    let paramsString =  '';
    for(let key in config.data){
     
      paramsString += `${key}=${config.data[key]}&`
   
    }
    config.data = paramsString.slice(0,-1)
  }
  return config;
})


Vue.use(VueAxios, axios)


new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
